import {
  SfSidebar,
  SfButton,
  SfIcon,
  SfHeading,
  SfFilter,
  SfProductCard,
  SfProductCardHorizontal,
  SfPagination,
  SfAccordion,
  SfSelect,
  SfBreadcrumbs,
  SfLoader,
  SfColor,
  SfProperty
} from '@storefront-ui/vue';
import { computed, onMounted } from '@vue/composition-api';
import {
  useCart,
  useWishlist,
  productGetters,
  useFacet,
  facetGetters
} from '@vue-storefront/shopify';
import { useUiHelpers, useUiState, useUiNotification } from '~/composables';
import { onSSR } from '@vue-storefront/core';

export default {
  transition: 'fade',
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  data() {
    return {
      currentCate: '',
      breadcrumbs: [
        {
          text: 'Home',
          route: { link: '/' }
        },
        {
          text: this.removeSpaceFromText(this.$route.params.slug_1),
          route: { link: '#' }
        }
      ]
    };
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  setup(props, context) {
    const th = useUiHelpers();
    const uiState = useUiState();
    const { addItem: addItemToCart, isInCart } = useCart();
    const { send: sendNotification } = useUiNotification();
    const { addItem: addItemToWishlist } = useWishlist();
    const { result, search, loading } = useFacet();
    const products = computed(() => facetGetters.getProducts(result.value));
    const sortBy = computed(() => facetGetters.getSortOptions(result.value));
    const facets = computed(() =>
      facetGetters.getGrouped(result.value, ['color', 'size'])
    );
    const pagination = computed(() => facetGetters.getPagination(result.value));
    onSSR(async () => {
      await search(th.getFacetsFromURL());
    });

    const { isFacetColor } = useUiHelpers();
    const { toggleCategoryGridView } = useUiState();

    onMounted(() => {
      context.root.$scrollTo(context.root.$el, 2000);
    });

    return {
      ...uiState,
      th,
      products,
      loading,
      productGetters,
      pagination,
      sortBy,
      facets,
      sendNotification,
      addItemToWishlist,
      addItemToCart,
      isInCart,
      isFacetColor,
      toggleCategoryGridView
    };
  },
  components: {
    SfButton,
    SfSidebar,
    SfIcon,
    SfFilter,
    SfProductCard,
    SfProductCardHorizontal,
    SfPagination,
    SfAccordion,
    SfSelect,
    SfBreadcrumbs,
    SfLoader,
    SfColor,
    SfHeading,
    SfProperty
  },
  methods: {
    // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
    HandleAddTocart(productObj) {
      this.addItemToCart(productObj).then(() => {
        this.sendNotification({
          key: 'added_to_cart',
          message: 'Product has been successfully added to cart !',
          type: 'success',
          title: 'Product added!',
          icon: 'check'
        });
      });
    },
    // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
    removeSpaceFromText(str) {
      let i;
      const frags = str.split('-');
      for (i = 0; i < frags.length; i++) {
        frags[i] = frags[i].charAt(0).toUpperCase() + frags[i].slice(1);
      }
      return frags.join(' ');
    }
  }
};
